.loginContainer, .registerContainer {
    width: 300px;
    position: relative;
    margin: 50px auto;
}

.formStyling {
  display: grid;
  margin-top: 10px;
  text-align: center;
}

.formStyling input {
    margin-top: 15px;
    /* margin:auto; */
    justify-items: center;
    margin-left:20px;
    margin-right:20px;
    font-size: large;
    border-radius: 5px;
    padding-left:5px;
}

.formStyling button{
    margin-top:10px;
    margin-bottom: 10px;
    font-size: large;
}